/* eslint-disable react-hooks/rules-of-hooks */
import React from 'react';
import { useAccessibility } from '../hooks/useAccessibility';
import { GalleryProps } from './Gallery';

export interface AccessabilityProps {
  disableAccessibilityHelper?: boolean;
}

export const WithAccessibility =
  <P extends GalleryProps>(Gallery: React.FunctionComponent<P>) =>
  (props: Omit<P, 'isAccessible'> & AccessabilityProps) => {
    const isAccessibilityEnabled = !props.disableAccessibilityHelper;
    const { isAccessible } = useAccessibility(isAccessibilityEnabled);

    return <Gallery {...(props as P)} isAccessible={isAccessible} />;
  };
